"use client"

import { CreateTestSuiteForm } from "@/app/components/create-test-suite-form"
import { ArrowLeft } from "lucide-react"
import Link from "next/link"

interface PageProps {
  params: {
    id: string
  }
}

export default function CreateTestSuitePage({ params }: PageProps) {
  return (
    <div className="container mx-auto py-8">
      <div className="mb-8">
        <Link
          href={`/projects/${params.id}/test-suites`}
          className="flex items-center text-muted-foreground hover:text-foreground"
        >
          <ArrowLeft className="mr-2 h-4 w-4" />
          Back to Test Suites
        </Link>
      </div>
      <div className="max-w-2xl mx-auto">
        <CreateTestSuiteForm />
      </div>
    </div>
  )
} 