"use client"

import { zodResolver } from "@hookform/resolvers/zod"
import { useForm, type SubmitHandler } from "react-hook-form"
import { z } from "zod"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { toast } from "sonner"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Switch } from "@/components/ui/switch"

const formSchema = z.object({
  name: z.string().min(3, {
    message: "Test suite name must be at least 3 characters.",
  }),
  description: z.string().optional(),
  parallelExecution: z.boolean(),
  retryFailedTests: z.boolean(),
  timeout: z.string(),
})

type FormValues = z.infer<typeof formSchema>

export function CreateTestSuiteForm() {
  const router = useRouter()

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: "",
      description: "",
      parallelExecution: false,
      retryFailedTests: false,
      timeout: "30",
    },
  })

  const onSubmit: SubmitHandler<FormValues> = (values) => {
    // In a real app, you would save the test suite to your backend
    console.log(values)

    toast.success("Test Suite Created", {
      description: `Successfully created "${values.name}" test suite`,
    })

    // Navigate back to the test suites list
    router.push("/")
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Create Test Suite</CardTitle>
        <CardDescription>Configure your new test suite with a name, description, and settings.</CardDescription>
      </CardHeader>
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)}>
          <Tabs defaultValue="basic">
            <TabsList className="grid w-full grid-cols-2">
              <TabsTrigger value="basic">Basic Information</TabsTrigger>
              <TabsTrigger value="advanced">Advanced Settings</TabsTrigger>
            </TabsList>
            <TabsContent value="basic">
              <CardContent className="space-y-6 pt-4">
                <FormField
                  control={form.control}
                  name="name"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Test Suite Name</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., API Integration Tests" {...field} />
                      </FormControl>
                      <FormDescription>A descriptive name for your test suite.</FormDescription>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="description"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Description</FormLabel>
                      <FormControl>
                        <Textarea
                          placeholder="Describe the purpose and scope of this test suite"
                          className="min-h-[120px]"
                          {...field}
                        />
                      </FormControl>
                      <FormDescription>Optional details about what this test suite covers.</FormDescription>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </CardContent>
            </TabsContent>
            <TabsContent value="advanced">
              <CardContent className="space-y-6 pt-4">
                <FormField
                  control={form.control}
                  name="parallelExecution"
                  render={({ field }) => (
                    <FormItem className="flex flex-row items-center justify-between rounded-lg border p-4">
                      <div className="space-y-0.5">
                        <FormLabel className="text-base">Parallel Execution</FormLabel>
                        <FormDescription>Run tests in parallel to speed up execution</FormDescription>
                      </div>
                      <FormControl>
                        <Switch checked={field.value} onCheckedChange={field.onChange} />
                      </FormControl>
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="retryFailedTests"
                  render={({ field }) => (
                    <FormItem className="flex flex-row items-center justify-between rounded-lg border p-4">
                      <div className="space-y-0.5">
                        <FormLabel className="text-base">Retry Failed Tests</FormLabel>
                        <FormDescription>Automatically retry tests that fail</FormDescription>
                      </div>
                      <FormControl>
                        <Switch checked={field.value} onCheckedChange={field.onChange} />
                      </FormControl>
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="timeout"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Test Timeout (seconds)</FormLabel>
                      <FormControl>
                        <Input type="number" min="1" {...field} />
                      </FormControl>
                      <FormDescription>Maximum time in seconds before a test is marked as failed</FormDescription>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </CardContent>
            </TabsContent>
          </Tabs>
          <CardFooter className="flex justify-between border-t p-6">
            <Button variant="outline" type="button" onClick={() => router.push("/")}>
              Cancel
            </Button>
            <Button type="submit">Create Test Suite</Button>
          </CardFooter>
        </form>
      </Form>
    </Card>
  )
}
